import React from "react";
import "./tm.css";

class Footer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.onClick = this.onClick.bind(this);
    this.onTouchEnd = this.onTouchEnd.bind(this);
  }

  onClick(event) {
    console.log("onClick: Terms and Conditions");

    event.preventDefault();
    event.stopPropagation();

    window.open(
      "http://tidbo.dhp11.com/termsandconditions.html",
      "TIDBO - Terms and Conditions"
    );
  }

  onTouchEnd(event) {
    console.log("onTouchEnd: Terms and Conditions");

    event.preventDefault();
    event.stopPropagation();

    window.open(
      "http://tidbo.dhp11.com/termsandconditions.html",
      "TIDBO - Terms and Conditions"
    );
  }

  render() {
    return (
      <footer className="footerContainer">
        <div
          className="footerTermsAndConditions"
          onClick={this.onClick}
          onTouchEnd={this.onTouckEnd}
        >
          Terms and Conditions
        </div>
      </footer>
    );
  }
}

export default Footer;
