import React from "react";
import "./tm.css";

class Row extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.onClick = this.onClick.bind(this);
    this.onTouchEnd = this.onTouchEnd.bind(this);
  }

  onClick(event) {
    console.log("onClick: Terms and Conditions");

    event.preventDefault();
    event.stopPropagation();
  }

  onTouchEnd(event) {
    console.log("onTouchEnd: Terms and Conditions");

    event.preventDefault();
    event.stopPropagation();
  }

  render() {
    return this.props.rows.map((row, i) => {
      return (
        <div className="sectionContainerRowLabelValue" key={i}>
          <div className="sectionContainerRowLabel">{row.name}:</div>
          <div className="sectionContainerRowValue">{row.value}</div>
        </div>
      );
    });
  }
}

export default Row;
