import React from "react";
import Menu from "./Menu";
import "./tm.css";

class Nav extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.menuOpen = false;

    this.onTouchMove = this.onTouchMove.bind(this);
    this.closeNav = this.closeNav.bind(this);
    this.openNav = this.openNav.bind(this);
  }

  onTouchMove(event) {
    console.log("onTouchMove: onTouchMove event");

    document.body.scrollTop = 0;
  }

  closeNav(event) {
    console.log("closeNav: onClick event");

    event.preventDefault();
    event.stopPropagation();

    //if (this.menuOpen)
    //{
    this.menuOpen = false;
    document
      .getElementById("mySidenavBackground")
      .classList.remove("backgroundDimVisibility");
    document.getElementById("mySidenav").classList.remove("sidenavDisplayMenu");
    document.getElementById("mySidenav").classList.add("sidenavHideMenu");
    //document.body.style.position="unset";
    document.body.classList.remove("bodyNoScroll");
    //}
  }

  openNav(event) {
    console.log("openNav: onClick event");

    event.preventDefault();
    event.stopPropagation();

    //if (null == this.timer)
    //{
    this.menuOpen = true;
    document
      .getElementById("mySidenavBackground")
      .classList.add("backgroundDimVisibility");
    document.getElementById("mySidenav").classList.remove("sidenavHideMenu");
    document.getElementById("mySidenav").classList.add("sidenavDisplayMenu");
    document.body.classList.add("bodyNoScroll");
    //document.body.style.position="fixed";
    //}
  }

  render() {
    const MENUITEMS = [
      {
        name: "Account1",
        onClick: "",
        onTouchStart: "",
        divVL: true,
        divHL: false
      },
      {
        name: "Account2",
        onClick: "",
        onTouchStart: "",
        divVL: false,
        divHL: false
      },
      {
        name: "Account3",
        onClick: "",
        onTouchStart: "",
        divVL: false,
        divHL: true
      },
      {
        name: "Account4",
        onClick: "",
        onTouchStart: "",
        divVL: true,
        divHL: true
      },
      {
        name: "Account5",
        onClick: "",
        onTouchStart: "",
        divVL: true,
        divHL: true
      },
      {
        name: "Account6",
        onClick: "",
        onTouchStart: "",
        divVL: true,
        divHL: true
      }
    ];

    return (
      <nav name="navPage1Home" className="navFixedHeader">
        <div
          id="mySidenavBackground"
          className="backgroundDim"
          onClick={this.closeNav}
          onTouchStart={this.closeNav}
        ></div>
        <div id="mySidenav" className="sidenav">
          <div className="navmenu">
            <div className="navitemheader" onTouchMove={this.onTouchMove}>
              <Menu menuitems={MENUITEMS} />
            </div>
          </div>
        </div>
      </nav>
    );
  }
}

