import React from 'react';
import tm_back from './images/tm_back.svg';
import tidbo_logo_color from './images/tidbo_logo_color.svg';
import tm_ham_menu from './images/tm_ham_menu.svg';
import './tm.css';

class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};

        this.onClickBack = this.onClickBack.bind(this);
        this.onClickLogout = this.onClickLogout.bind(this);
        this.onClickMenu = this.onClickMenu.bind(this);
      }

    onClickBack(event)
    { 
        console.log ("onBack");
        console.log ('click');
        event.preventDefault(); 
        event.stopPropagation();
    
    }

    onClickLogout(event)
    {
        console.log ("onLogout"); 
        console.log ('click'); 
        event.preventDefault(); 
        event.stopPropagation();
    }
    
    onClickMenu(event) {
        console.log ("onMenu"); 

        console.log ('click');
        event.preventDefault();
        event.stopPropagation();

		//this.menuOpen = true;
		document.getElementById("mySidenavBackground").classList.add("backgroundDimVisibility");
		document.getElementById("mySidenav").classList.remove("sidenavHideMenu");
		document.getElementById("mySidenav").classList.add("sidenavDisplayMenu");
		document.body.classList.add("bodyNoScroll");
		//document.body.style.position="fixed";
    }

    render() {
    return (
    <header id="myHeader" className="headerContainerOuter" >
        <div className="headerContainer" >
            <div className="headerBack">
                <img src={tm_back} width="25px" alt=""
                onClick={this.onClickBack}
                onTouchStart={this.onClickBack}
                ></img>
            </div>
                <div className="headerLogo">
                    <img src={ tidbo_logo_color } height="35px" alt=""
                    onClick={this.onClickBack}
                    onTouchStart={this.onClickBack}
                    ></img>
                </div>
                <div className="headerTitle"></div>
                <div className="headerLogout"
                                onClick={this.onClickLogout}
                                onTouchStart={this.onClickLogout}
                >Log Out</div>
                <div className="headerMenu">
                    <img src={tm_ham_menu} width="30px" alt=""
                    onClick={this.onClickMenu}
                    onTouchStart={this.onClickMenu}
                    ></img>
                </div>
              
        </div>
    </header>
    )
    }
}


