import React from "react";
import SectionContainerRow from "./SectionContainerRow";
import "./tm.css";

class Section extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.onClick = this.onClick.bind(this);
    this.onTouchEnd = this.onTouchEnd.bind(this);
  }

  onClick(event) {
    console.log("onClick: Terms and Conditions");

    event.preventDefault();
    event.stopPropagation();

    window.open(
      "http://tidbo.dhp11.com/termsandconditions.html",
      "TIDBO - Terms and Conditions"
    );
  }

  onTouchEnd(event) {
    console.log("onTouchEnd: Terms and Conditions");

    event.preventDefault();
    event.stopPropagation();

    window.open(
      "http://tidbo.dhp11.com/termsandconditions.html",
      "TIDBO - Terms and Conditions"
    );
  }

  render() {
    return (
      <div
        className="sectionContainer sectionContainerFixedHeader"
        name={this.props.section.page}
      >
        <div className="sectionContainerRow sectionContainerHeader">
          {this.props.section.title}
        </div>
        <SectionContainerRow />
      </div>
    );
  }
}

export default Section;
